___CSS_LOADER_EXPORT___.push([module.id, ".category-detail[data-v-6a150966]{margin:0 auto;max-width:1000px}.info-box[data-v-6a150966]{border-radius:.25rem;margin-bottom:.5rem;padding:.75rem}.info-box-content[data-v-6a150966]{text-align:center}.info-box-text[data-v-6a150966]{font-size:.875rem;font-weight:600}.info-box-number[data-v-6a150966]{font-size:1.5rem;font-weight:700}.text-justify[data-v-6a150966]{text-align:justify}.d-grid[data-v-6a150966]{display:grid}.gap-2[data-v-6a150966]{gap:.5rem}@media (max-width:768px){.card-tools[data-v-6a150966]{align-items:stretch;flex-direction:column}.card-tools .btn[data-v-6a150966]{margin-bottom:.25rem}}", "",{"version":3,"sources":["webpack://./resources/js/components/forms/CategoryDetail.vue"],"names":[],"mappings":"AA6XA,kCAEA,aAAA,CADA,gBAEA,CAEA,2BACA,oBAAA,CAEA,mBAAA,CADA,cAEA,CAEA,mCACA,iBACA,CAEA,gCACA,iBAAA,CACA,eACA,CAEA,kCACA,gBAAA,CACA,eACA,CAEA,+BACA,kBACA,CAEA,yBACA,YACA,CAEA,wBACA,SACA,CAEA,yBACA,6BAEA,mBAAA,CADA,qBAEA,CAEA,kCACA,oBACA,CACA","sourcesContent":["<template>\r\n    <div class=\"category-detail\">\r\n        <div class=\"card\">\r\n            <div class=\"card-header\">\r\n                <h3 class=\"card-title\">\r\n                    <i class=\"fas fa-tags mr-2\"></i>\r\n                    Detalles de la Categoría\r\n                </h3>\r\n                <div class=\"card-tools\">\r\n                    <router-link :to=\"{ name: 'categories' }\" class=\"btn btn-outline-secondary btn-sm\">\r\n                        <i class=\"fas fa-arrow-left mr-1\"></i>\r\n                        Volver a Categorías\r\n                    </router-link>\r\n                    <router-link \r\n                        v-if=\"canEdit\" \r\n                        :to=\"{ name: 'categories.edit', params: { id: categoryId } }\" \r\n                        class=\"btn btn-primary btn-sm ml-2\"\r\n                    >\r\n                        <i class=\"fas fa-edit mr-1\"></i>\r\n                        Editar\r\n                    </router-link>\r\n                </div>\r\n            </div>\r\n\r\n            <div v-if=\"loading\" class=\"card-body text-center\">\r\n                <div class=\"spinner-border text-primary\" role=\"status\">\r\n                    <span class=\"sr-only\">Cargando...</span>\r\n                </div>\r\n                <p class=\"mt-3\">Cargando información de la categoría...</p>\r\n            </div>\r\n\r\n            <div v-else-if=\"category\" class=\"card-body\">\r\n                <div class=\"row\">\r\n                    <!-- Información principal -->\r\n                    <div class=\"col-md-8\">\r\n                        <h4 class=\"mb-3\">{{ category.name }}</h4>\r\n                        \r\n                        <div v-if=\"category.description\" class=\"mb-4\">\r\n                            <h6 class=\"text-muted mb-2\">Descripción</h6>\r\n                            <p class=\"text-justify\">{{ category.description }}</p>\r\n                        </div>\r\n\r\n                        <div class=\"row mb-4\">\r\n                            <div class=\"col-md-6\">\r\n                                <h6 class=\"text-muted mb-2\">Rango de Edad</h6>\r\n                                <p class=\"mb-1\">\r\n                                    <i class=\"fas fa-birthday-cake text-primary mr-2\"></i>\r\n                                    <span v-if=\"category.min_age || category.max_age\">\r\n                                        {{ getAgeRange() }}\r\n                                    </span>\r\n                                    <span v-else class=\"text-muted\">Sin restricción de edad</span>\r\n                                </p>\r\n                            </div>\r\n                            <div class=\"col-md-6\">\r\n                                <h6 class=\"text-muted mb-2\">Género</h6>\r\n                                <p class=\"mb-1\">\r\n                                    <i class=\"fas fa-venus-mars text-primary mr-2\"></i>\r\n                                    {{ getGenderLabel() }}\r\n                                </p>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <div class=\"row mb-4\">\r\n                            <div class=\"col-md-6\">\r\n                                <h6 class=\"text-muted mb-2\">Estado</h6>\r\n                                <span :class=\"getStatusClass(category.status)\">\r\n                                    {{ getStatusLabel(category.status) }}\r\n                                </span>\r\n                            </div>\r\n                            <div class=\"col-md-6\">\r\n                                <h6 class=\"text-muted mb-2\">Tipo</h6>\r\n                                <span :class=\"category.is_competitive ? 'badge badge-success' : 'badge badge-info'\">\r\n                                    <i :class=\"category.is_competitive ? 'fas fa-trophy' : 'fas fa-heart'\" class=\"mr-1\"></i>\r\n                                    {{ category.is_competitive ? 'Competitiva' : 'Recreativa' }}\r\n                                </span>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <div class=\"mb-4\">\r\n                            <h6 class=\"text-muted mb-2\">Estadísticas</h6>\r\n                            <div class=\"row\">\r\n                                <div class=\"col-md-4\">\r\n                                    <div class=\"info-box bg-primary\">\r\n                                        <div class=\"info-box-content\">\r\n                                            <span class=\"info-box-text text-white\">Total Pilotos</span>\r\n                                            <span class=\"info-box-number text-white\">{{ category.pilots_count || 0 }}</span>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"col-md-4\">\r\n                                    <div class=\"info-box bg-success\">\r\n                                        <div class=\"info-box-content\">\r\n                                            <span class=\"info-box-text text-white\">Activos</span>\r\n                                            <span class=\"info-box-number text-white\">{{ category.active_pilots_count || 0 }}</span>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"col-md-4\">\r\n                                    <div class=\"info-box bg-info\">\r\n                                        <div class=\"info-box-content\">\r\n                                            <span class=\"info-box-text text-white\">En Competencias</span>\r\n                                            <span class=\"info-box-number text-white\">{{ category.competitions_count || 0 }}</span>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <!-- Fechas de registro -->\r\n                        <div class=\"row text-muted small\">\r\n                            <div class=\"col-md-6\">\r\n                                <p class=\"mb-1\">\r\n                                    <i class=\"fas fa-plus-circle mr-1\"></i>\r\n                                    Creada: {{ formatDate(category.created_at) }}\r\n                                </p>\r\n                            </div>\r\n                            <div class=\"col-md-6\">\r\n                                <p class=\"mb-1\">\r\n                                    <i class=\"fas fa-edit mr-1\"></i>\r\n                                    Última actualización: {{ formatDate(category.updated_at) }}\r\n                                </p>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <!-- Panel lateral -->\r\n                    <div class=\"col-md-4\">\r\n                        <div class=\"card bg-light\">\r\n                            <div class=\"card-header\">\r\n                                <h6 class=\"card-title mb-0\">\r\n                                    <i class=\"fas fa-info-circle mr-1\"></i>\r\n                                    Resumen de la Categoría\r\n                                </h6>\r\n                            </div>\r\n                            <div class=\"card-body\">\r\n                                <dl class=\"row\">\r\n                                    <dt class=\"col-sm-5\">Nombre:</dt>\r\n                                    <dd class=\"col-sm-7\">{{ category.name }}</dd>\r\n                                    \r\n                                    <dt class=\"col-sm-5\">Edades:</dt>\r\n                                    <dd class=\"col-sm-7\">{{ getAgeRange() || 'Sin límite' }}</dd>\r\n                                    \r\n                                    <dt class=\"col-sm-5\">Género:</dt>\r\n                                    <dd class=\"col-sm-7\">{{ getGenderLabel() }}</dd>\r\n                                    \r\n                                    <dt class=\"col-sm-5\">Tipo:</dt>\r\n                                    <dd class=\"col-sm-7\">{{ category.is_competitive ? 'Competitiva' : 'Recreativa' }}</dd>\r\n                                    \r\n                                    <dt class=\"col-sm-5\">Estado:</dt>\r\n                                    <dd class=\"col-sm-7\">\r\n                                        <span :class=\"getStatusClass(category.status)\">\r\n                                            {{ getStatusLabel(category.status) }}\r\n                                        </span>\r\n                                    </dd>\r\n                                </dl>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <!-- Acciones rápidas -->\r\n                        <div v-if=\"canEdit\" class=\"card mt-3\">\r\n                            <div class=\"card-header\">\r\n                                <h6 class=\"card-title mb-0\">\r\n                                    <i class=\"fas fa-tools mr-1\"></i>\r\n                                    Acciones\r\n                                </h6>\r\n                            </div>\r\n                            <div class=\"card-body\">\r\n                                <div class=\"d-grid gap-2\">\r\n                                    <router-link \r\n                                        :to=\"{ name: 'categories.edit', params: { id: categoryId } }\" \r\n                                        class=\"btn btn-primary btn-sm\"\r\n                                    >\r\n                                        <i class=\"fas fa-edit mr-1\"></i>\r\n                                        Editar Categoría\r\n                                    </router-link>\r\n                                    <router-link \r\n                                        :to=\"{ name: 'pilots', query: { category: categoryId } }\" \r\n                                        class=\"btn btn-outline-primary btn-sm\"\r\n                                    >\r\n                                        <i class=\"fas fa-users mr-1\"></i>\r\n                                        Ver Pilotos\r\n                                    </router-link>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n\r\n                <!-- Lista de pilotos (si existen) -->\r\n                <div v-if=\"pilots && pilots.length > 0\" class=\"mt-5\">\r\n                    <h5 class=\"mb-3\">\r\n                        <i class=\"fas fa-user-ninja mr-2\"></i>\r\n                        Pilotos en esta Categoría ({{ pilots.length }})\r\n                    </h5>\r\n                    <div class=\"table-responsive\">\r\n                        <table class=\"table table-sm table-hover\">\r\n                            <thead class=\"thead-light\">\r\n                                <tr>\r\n                                    <th>Nombre</th>\r\n                                    <th>Número</th>\r\n                                    <th>Club</th>\r\n                                    <th>Edad</th>\r\n                                    <th>Estado</th>\r\n                                    <th>Acciones</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr v-for=\"pilot in pilots\" :key=\"pilot.id\">\r\n                                    <td>{{ pilot.first_name }} {{ pilot.last_name }}</td>\r\n                                    <td>{{ pilot.number }}</td>\r\n                                    <td>\r\n                                        <span v-if=\"pilot.club\">{{ pilot.club.name }}</span>\r\n                                        <span v-else class=\"text-muted\">Sin club</span>\r\n                                    </td>\r\n                                    <td>{{ pilot.age || 'N/A' }}</td>\r\n                                    <td>\r\n                                        <span :class=\"getPilotStatusClass(pilot.status)\">\r\n                                            {{ getPilotStatusLabel(pilot.status) }}\r\n                                        </span>\r\n                                    </td>\r\n                                    <td>\r\n                                        <router-link \r\n                                            :to=\"{ name: 'pilots.show', params: { id: pilot.id } }\" \r\n                                            class=\"btn btn-sm btn-outline-primary\"\r\n                                        >\r\n                                            <i class=\"fas fa-eye\"></i>\r\n                                        </router-link>\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </table>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n\r\n            <div v-else class=\"card-body text-center\">\r\n                <i class=\"fas fa-exclamation-triangle fa-3x text-warning mb-3\"></i>\r\n                <h4 class=\"text-muted\">Categoría no encontrada</h4>\r\n                <p class=\"text-muted\">La categoría que buscas no existe o ha sido eliminada.</p>\r\n                <router-link :to=\"{ name: 'categories' }\" class=\"btn btn-primary\">\r\n                    <i class=\"fas fa-arrow-left mr-1\"></i>\r\n                    Volver a Categorías\r\n                </router-link>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: 'CategoryDetail',\r\n    data() {\r\n        return {\r\n            category: null,\r\n            pilots: [],\r\n            loading: false\r\n        };\r\n    },\r\n    computed: {\r\n        categoryId() {\r\n            return this.$route.params.id;\r\n        },\r\n        canEdit() {\r\n            return window.Laravel?.user?.authenticated || false;\r\n        }\r\n    },\r\n    mounted() {\r\n        this.loadCategory();\r\n        this.loadCategoryPilots();\r\n    },\r\n    methods: {\r\n        async loadCategory() {\r\n            this.loading = true;\r\n            try {\r\n                const response = await fetch(`/api/categories/${this.categoryId}`);\r\n                if (!response.ok) {\r\n                    throw new Error('Categoría no encontrada');\r\n                }\r\n                const data = await response.json();\r\n                this.category = data;\r\n            } catch (error) {\r\n                console.error('Error loading category:', error);\r\n                this.category = null;\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        async loadCategoryPilots() {\r\n            try {\r\n                const response = await fetch(`/api/categories/${this.categoryId}/pilots`);\r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    this.pilots = data.data || data;\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading category pilots:', error);\r\n            }\r\n        },\r\n\r\n        getAgeRange() {\r\n            if (!this.category) return '';\r\n            \r\n            const min = this.category.min_age;\r\n            const max = this.category.max_age;\r\n            \r\n            if (min && max) {\r\n                return `${min} - ${max} años`;\r\n            } else if (min) {\r\n                return `${min}+ años`;\r\n            } else if (max) {\r\n                return `Hasta ${max} años`;\r\n            }\r\n            return '';\r\n        },\r\n\r\n        getGenderLabel() {\r\n            if (!this.category) return 'Mixto';\r\n            \r\n            const labels = {\r\n                'male': 'Masculino',\r\n                'female': 'Femenino',\r\n                '': 'Mixto'\r\n            };\r\n            return labels[this.category.gender] || 'Mixto';\r\n        },\r\n\r\n        getStatusClass(status) {\r\n            const classes = {\r\n                'active': 'badge badge-success',\r\n                'inactive': 'badge badge-secondary'\r\n            };\r\n            return classes[status] || 'badge badge-secondary';\r\n        },\r\n\r\n        getStatusLabel(status) {\r\n            const labels = {\r\n                'active': 'Activa',\r\n                'inactive': 'Inactiva'\r\n            };\r\n            return labels[status] || 'Desconocido';\r\n        },\r\n\r\n        getPilotStatusClass(status) {\r\n            const classes = {\r\n                'active': 'badge badge-success',\r\n                'inactive': 'badge badge-secondary',\r\n                'suspended': 'badge badge-danger'\r\n            };\r\n            return classes[status] || 'badge badge-secondary';\r\n        },\r\n\r\n        getPilotStatusLabel(status) {\r\n            const labels = {\r\n                'active': 'Activo',\r\n                'inactive': 'Inactivo',\r\n                'suspended': 'Suspendido'\r\n            };\r\n            return labels[status] || 'Desconocido';\r\n        },\r\n\r\n        formatDate(dateString) {\r\n            if (!dateString) return 'No disponible';\r\n            try {\r\n                const date = new Date(dateString);\r\n                return date.toLocaleDateString('es-ES', {\r\n                    year: 'numeric',\r\n                    month: 'long',\r\n                    day: 'numeric',\r\n                    hour: '2-digit',\r\n                    minute: '2-digit'\r\n                });\r\n            } catch (error) {\r\n                return 'Fecha inválida';\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.category-detail {\r\n    max-width: 1000px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.info-box {\r\n    border-radius: 0.25rem;\r\n    padding: 0.75rem;\r\n    margin-bottom: 0.5rem;\r\n}\r\n\r\n.info-box-content {\r\n    text-align: center;\r\n}\r\n\r\n.info-box-text {\r\n    font-size: 0.875rem;\r\n    font-weight: 600;\r\n}\r\n\r\n.info-box-number {\r\n    font-size: 1.5rem;\r\n    font-weight: bold;\r\n}\r\n\r\n.text-justify {\r\n    text-align: justify;\r\n}\r\n\r\n.d-grid {\r\n    display: grid;\r\n}\r\n\r\n.gap-2 {\r\n    gap: 0.5rem;\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .card-tools {\r\n        flex-direction: column;\r\n        align-items: stretch;\r\n    }\r\n    \r\n    .card-tools .btn {\r\n        margin-bottom: 0.25rem;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
